.container {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.containerInner {
  position: relative;
  padding: 0 20px;
}

.h2 {
  color: #7cc4e6;
}

.templateWrapper {
  display: table;
}

.buttonWrapper {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 39px 0;
  padding-left: 48px;
  background-color: #fcfcfc;
}

.button {
  padding: 10px 24px;
  border-radius: 4px;
  border: 0;
  background-color: #b3ba51;
  font-family: Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
}

@media all and (max-width: 768px) {
  .container {
    width: 100%;
    padding: 20px;
  }

  .button {
    position: static;
    margin: 20px 0;
  }

  .buttonWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0;
  }
}



