.container {
  padding: 20px 40px;
}

@media all and (max-width: 768px) {
  .container {
    padding: 20px;
  }
}

.policy {
  margin-top: 22px;
}

.toCustomWrapper {
  padding: 20px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.toCustom {
  padding: 10px 24px;
  border-radius: 4px;
  border: 0;
  background-color: #b3ba51;
  font-family: Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}



